.hide {
  transform: translateY(-70px);
  transition: 0.5s;
}

.body_hidden {
  overflow: hidden;
}

header {
  display: flex;
  justify-content: center;
  height: 52px;
  position: sticky;
  top: 0;
  z-index: 10;
  color: #d4ddfb;
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  width: 100%;
  line-height: 150%;
  letter-spacing: 0.04em;
}

.header__wrapper > nav > ul {
  list-style-type: none;
  display: flex;
  font-family: 'Press Start 2P';
  gap: 24px;
  padding: 0;
}

.header__wrapper > nav > ul > li {
  cursor: pointer;
}

.header__wrapper > nav > ul a > span {
 color: #C778DD;
}


.header_background {
  backdrop-filter: blur(10px);
  border-bottom: 1px solid gray;
}

.header__wrapper {
  max-width: 1340px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 20px;
}

@media (max-width: 660px) {
  .header__wrapper > nav > ul {
    display: none;
  }
}
